
//
//eneric Values



html {

	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%; }

body {

	margin: 0;
	font: 16px/1 sans-serif;
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	box-sizing: border-box; }

*, *:after, *:before {

	box-sizing: inherit; }

h1, h2, h3, h4, p, blockquote, figure, ol, ul {

	margin: 0;
	padding: 0; }

main, li {

	display: block; }

h1, h2, h3, h4 {

	font-size: inherit; }

p {

	margin: 1.5em 0; }

strong {

	font-weight: bold; }

a, button {

	color: inherit; }

a {

	text-decoration: none; }

button {

	overflow: visible;
	border: 0;
	font: inherit;
	-webkit-font-smoothing: inherit;
	letter-spacing: inherit;
	background: none;
	cursor: pointer; }

::-moz-focus-inner {

	padding: 0;
	border: 0; }

:focus {

	outline: 0; }

img {

	max-width: 100%;
	height: auto;
	border: 0; }









//
//efault Markup



.clearfix {

	&:after {

		content: "";
		display: table;
		clear: both; } }
